import { WatchlistDataTableTextCell } from "../../WatchlistDataTableCells/WatchlistDataTableTextCell"
import { genericColumn } from "../../genericColumn"

function totalAnalysts(_tableName: string) {
  const title = "Total Analysts"
  return genericColumn({
    endpoint: "companyAnalystRatings",
    columnKeys: ["totalAnalysts"],
    size: 100,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Total number of analysts providing ratings for this company",
    cell: info => {
      const data = info.row.original.financialData.companyAnalystRatings?.data
      const total =
        (data?.strongBuy || 0) +
        (data?.buy || 0) +
        (data?.hold || 0) +
        (data?.sell || 0) +
        (data?.strongSell || 0)

      return (
        <WatchlistDataTableTextCell
          queryResult={info.row.original.financialData.companyAnalystRatings}
          value={total > 0 ? total : undefined}
        >
          {total > 0 ? total : "-"}
        </WatchlistDataTableTextCell>
      )
    },
  })
}

export { totalAnalysts }
